
#page-content-wrapper-wifi {
    max-width: 1350px;
    margin: 0 auto;
    
    sup {
        font-size: 13px;
    }
    .feature-icon-text {
        sup {
            font-size: 13px;
            font-family: "Baskerville BT WGL4 W05 Roman",serif;
        }
    }
    
    /*** header ***/
    
    .header-wifi {
        height: 65px;
    }
    .header-desktop.header-grande-vista {
        justify-content: space-between;
        .header-title {
            margin-right: 40px;
        }
    }
    .mobile-block {
        display: none;
    }
    .header-logo{
        background-color: #009587;
        padding: 18px 0;
        width: 30%;
        img{
            width: 200px;
            margin: 0 auto;
            display: block;
        }
    }
    .header-title {
        h2{
            margin-left: 1rem;
            text-transform: uppercase;
            font-family: "Kessel_105 W05 Bold", sans-serif;
            font-size: 24px;
            color: #009587;
                position: relative;
                top: 58%;
                transform: translateY(-50%);
                text-align: center;
            span {
                font-family: "Kessel_105 W05 Heavy", sans-serif;
                position: relative;
                line-height: 1;
            }
        }
    }
    .header-call-us{
        margin-left: auto;
        a{

            color: #645f5a;
            text-transform: uppercase;
            font-family: "Kessel_105 W05 Bold",sans-serif;
            font-size: 12.5px;
            text-decoration: underline;
            display: block;
            position: relative;
            top: 58%;
            -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
            text-align: center;
            padding-right: 40px;
            i{
                font-size: 27px;
                margin-right: 6px;
                vertical-align: middle;
            }
        }
    }
    .header-mobile {
        background-color: #009587;
        .header-logo{
            width: auto;
            padding: 16.5px 10px;
            img{
                width: 225px;
            }
        }
        .header-call-us {
            a{
                color: #fff;
                font-size: 15.5px;
                padding-right: 12px;
                top: 60%;
                i{
                    font-size: 32px;
                }
            }
        }
    }


    @media ( min-width:768px ) {
        .header-mobile {
            display: none!important;
        }
    }
    @media ( max-width:767px ) {
        .header-desktop {
            display: none!important;
        }
        .header-mobile {
            display: block;
        }
        .mobile-block {
            display: block;
        }
        .mobile-header-title {
            padding-top: 8px;
            border-bottom: 2px solid #d5d5d5;
            margin: 0 13px;
            h2{
                text-transform: uppercase;
                font-family: "Kessel_105 W05 Bold", sans-serif;
                font-size: 21px;
                color: #009587;
                text-align: center;
                span {
                    font-family: "Kessel_105 W05 Heavy", sans-serif;
                    position: relative;
                    line-height: 1;
                }
            }
        }
    }
    
    /*** content ***/
    
    .content-wifi{
        background-color: #F8F8F7;
        background-position: left bottom;
        background-repeat: no-repeat;
        background-image: url(/wp-content/plugins/mvcweb/assets/mvcweb/css/landing/mvc-wifi/marriott_wifi_bg.svg);
        padding: 20px 25px 13px;
        background-size: 40%;
        img{
            width: 100%;
        }
        .image-block {
            .feature-icon-img {
                height: 210px;
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
            }
        }
        .ticket-wrapper{
            content: "";
            position: absolute;
            top: 5px;
            right: 0;
            display: block;
            width: calc(100% - 20px);
            height: calc(100% - 10px);
            border: 1px solid #d5d5d5;
        }
        .description-block {
            padding: 5px 0;
            .item-description {
                background-color: #fff;
                height: 100%;
                padding-left: 20px;
                padding-right: 10px;
                align-items: center;
                display: flex;
                h3{
                    font-family: "Baskerville BT WGL4 W05 Roman",serif;
                    color: #6f6259;
                    font-size: 21px;
                }
                p {
                    font-family: "Kessel_105 W05 Book";
                    color: #5f5f5f;
                    font-size: 13.5px;
                    span {
                        color: #009587;
                    }
                }
            }
        }
        .feature-icon {
            margin-top: 12px;
            .feature-icon-content {
                h1 {
                    font-family: "Baskerville BT WGL4 W05 Roman",serif;
                    color: #646464;
                    font-size: 21px;
                }
                h4 {
                    font-family: "Kessel_105 W05 Book";
                    color: #5f5f5f;
                    font-size: 16px;
                    letter-spacing: 1px;
                    line-height: 1.5;
                }
                .break {
                    margin-bottom: 20px;
                    margin-top: 13px;
                    &:after {
                        content: "";
                        background-color: #159486;
                        height: 4px;
                        width: 34px;
                        display: block;
                        top: -1px;
                        position: relative;
                    }
                    hr{
                        display: block;
                        height: 1px;
                        border: 0;
                        border-top: 2px solid #d8d8d8;
                        margin: 0;
                        padding: 0;
                    }
                }
                .feature-icon-img {
                    margin-bottom: 17px;
                    height: 267px;
                    overflow: hidden;
                    background-position: center;
                    background-repeat: no-repeat;
                    background-size: cover;
                }
            }
        }
    }
    
    @media ( min-width:768px ) {
        .image-block {
            padding-right: 0;
        }
        .desktop-block {
            display: block;
        }
    }
    
    @media ( max-width:767px ) {
        .desktop-block {
            display: none!important;
        }   
        .content-wifi {
            background-image:none;
            background-color: #fff;
            padding: 7px 13px 0px;
            >.container-fluid {
                padding: 0px;
            }
            .offers-tickets {
                margin: 0px;
                margin-bottom: 3px !important;
                .image-block {
                    padding: 0;
                    .feature-icon-img {
                        height: 166px;
                    }
                }
            }
            .ticket-wrapper {
                top: 0;
                width: 100%;
                height: 100%;
            }
            .description-block {
                .item-description {
                    padding: 0 25px;
                    h3 {
                        font-family: "Kessel_105 W05 Book",sans-serif;
                        color: #009587;
                        font-size: 16px;
                        text-transform: uppercase;
                        text-align: center;
                        letter-spacing: 1px;
                        margin-top: 10px;
                        margin-bottom: 5px;
                    }
                    p {
                        text-align: center;
                        line-height: 1.2;
                        margin-bottom: 10px;
                    }
                } 
            } 
        
            .feature-icon {
                margin-top: 1px;
                .feature-icon-content {
                    .feature-icon-img {
                        margin-bottom: 7px;
                        position: relative;
                        height: 237px;
                        .feature-icon-text {
                            font-family: "Baskerville BT WGL4 W05 Roman",serif;
                            font-size: 16px;
                            color: #fff;
                            position: absolute;
                            text-align: center;
                            padding: 18px 20px 17px;
                            bottom: 0;
                            width: 100%;
                            background: rgba(0, 0, 0, 0.4);
                            margin-bottom: 0px;
                        }
                    }
                } 
            }
        }
    }
    
    /*** footer ***/
    
    .footer-wifi {
        padding: 5px 40px 13px;
        .footerinfo1 {
            text-align: left !important;
            p {
                font-family: "Arial";
                color: #8B8B8B;
                font-size: 9.8px;
                margin-bottom: 7px;
                line-height: 1.2;
            }
        }
        .footerinfo2 {
            span {
                float: left;
                display: inline;
                margin-right: 19px;
            }
            p {
                text-transform: uppercase;
                font-size: 11.2px;
                color: #8B8B8B;
                line-height: 1.2;
                margin-top: 12px;
                margin-bottom: 3px;
            }
        }
        .footerinfo3 {
            text-align: left !important;
            p {
                font-family: "Arial";
                color: #8B8B8B;
                font-size: 9.8px;
                margin-bottom: 7px;
                line-height: 1.2;
            }
        }
        .footer-mg {
            float: right;
            display: inline-block;
        }
    }
    @media ( max-width:767px ) {
        .footer-wifi {
            padding: 0px 13px 9px;
            .footerinfo1{
                text-align: left !important;
                p{
                    font-size: 7.8px;
                }
            }
            .footerinfo2 {
                p {
                    font-size: 8.3px;
                    margin-top: 6px;
                }
                span {
                    margin-right: 9px;
                    img{
                        width: 50px;
                    }
                }
            }
            .wrapp-column-reverse {
                display: flex;
                flex-direction: column-reverse!important;
                padding-top: 3px !important;
                p {
                    font-size: 7.8px;
                    margin-bottom: 5px;
                    line-height: 1.3;
                }
            }
            
        }
    }
    
    /*** Connect to Our Resort WiFi ***/
    .header-connect-page {
        margin: 0 auto;
    }
    .container-wifi-form {
        margin-top: 40px;
        .title-form {
            font-family: "Baskerville BT WGL4 W05 Roman",serif;
            color: #6f6259;
            font-size: 26px;
            &:after {
                content: "";
                background-color: #159486;
                height: 4px;
                width: 35px;
                display: block;
                margin: 14px auto 20px;
            }
        }
        .wifi-form {
            max-width: 675px;
            background-color: #EFEFEE;
            padding: 25px 40px 3px;
            margin-bottom: 40px;
            .form-control {
                height: 50px;
                border-radius: 0;
                background-color: #F7F7F6;
                color: #6f6259;
                &::placeholder{
                    font-size: 16.8px;
                    color: #6f6259;
                    font-family: "Kessel_105 W05 Book";
                }
            }
            input[type="submit"] {
                background-color: #159486;
                border-radius: 0;
                color: #fff;
                font-size: 21px;
                text-transform: uppercase;
                letter-spacing: 2px;
                font-weight: 600;
                height: 45px;
                width: 230px;
                margin-top: 7px;
                margin-bottom: 15px;
            }
        }
        .trouble {
            color: #6f6259;
            font-family: Avenir,sans-serif;
            font-size: 14px;
        }
        .options-wifi,
        .accesscode-wifi {
            a{
                color: #6f6259;
                font-family: Avenir,sans-serif;
                font-size: 14px;
                text-decoration: underline;
            }
        }
        .accesscode-wifi {
            margin-top: 7px;
            margin-bottom: 27px;
        }
        .input-col {
            padding-left: 10px;
            padding-right: 10px;
        }

    }
    @media ( max-width:400px ) {
        .footer-wifi{
            .footer-mg {
                float: none;
                display: block;
                margin-top: 4px;
            }
        }
    }

}
